.whole-page {
  height: 100vh;
  background-image: url(http://i.imgur.com/nfuwDtz.jpg);
  background-attachment: fixed;
  overflow-y: scroll;
}

.whole-page p {
  font-size: 20px;
}

.imageHolder {
  height: 305px;
  width: 542px;
}

.imageHolder h3,
.weapon-container-b h3 {
  position: absolute;
  color: white;
  font-size: 30px;
  padding: 10px;
  filter: drop-shadow(2px 4px 6px black);
  z-index: 100;
}

.wz-drop-down {
  margin: auto;
  margin-top: 30px;
  text-align: center;
  background: #434040b3;
  padding: 10px;
  width: 400px;
}

.weapon-info,
.dropbox-data-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  #wz-best-wep {
    color: palegreen;
    text-decoration: none;
    font-weight: bolder;
    font-size: 1.5rem;
  }
}

.weapon-info,
.weapon-container-b,
.imageHolder,
.dropbox-data-content {
  width: 48%;
}

.imageHolder img {
  width: 95%;
  height: 100%;
  margin-left: 20px;
  margin-top: 23.5px;
}

.dropbox-container,
.second-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background: #b1e9f06e;
  filter: drop-shadow(2px 4px 6px black)
}

.second-row {
  margin-top: 32px;
}

.second-row img {
  background: #425158c7;
  width: 95%;
  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 3px;
}

.second-row p {
  text-align: right;
}

.dropbox-container p {
  text-align: left;
}

.main-container {
  display: flex;
  flex-direction: column;
  width: 60%;
  margin: auto;
  top: 50%;
  margin-top: 30px;
  background: #434040b3;
  padding: 20px;
  font-weight: bolder;
  p {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}

.second-row p {
  margin-top: 10px;
  margin-bottom: 10px;
}

.main-container h3 {
  margin: inherit;
}

.heading-details {
  font-weight: bolder;
  font-size: 50px;
  text-align: center;
  // text-decoration: underline;
}

#imageHolderName {
  margin-left: 20px;
  margin-top: 20px;
}

#playlist {
  font-size: 25px;
  color: white;
}

.most-used-tool {
  text-decoration: underline;
}

.notice-container {
  p {
    filter: drop-shadow(1px 1px black);
    color: white;
  }
}